import { extend } from 'flarum/extend';
import IndexPage from 'flarum/forum/components/IndexPage';
import Button from 'flarum/components/Button';
import Alert from 'flarum/common/components/Alert';

app.initializers.add('ziven-checkin', () => {
  // function checkForumLoadingComplete() {
  //   window.removeEventListener('DOMContentLoaded', checkForumLoadingComplete);
  // }

  // window.addEventListener('DOMContentLoaded', checkForumLoadingComplete);

  extend(IndexPage.prototype, 'sidebarItems', function(items) {
    if(app.session.user!==null){
      const canCheckin = app.session.user.attribute("canCheckin");
      const serverData = app.session.user.attribute("serverDate");

      if(canCheckin===true){
        items.add('forum-checkin', Button.component({
          icon: 'fas fa-calendar',
          className: 'Button Button--yellow',
          itemClassName: 'App-primaryControl',
          // style: 'width:190px',
          onclick: () => {
            app.session.user.save({lastCheckinTime:"currentTime",canCheckin:false});
            
            const alertKey = app.alerts.show(Alert, { type: 'success' }, app.translator.trans('签到成功'));
          },
        }, app.translator.trans('ziven-checkin.forum.check-in')));
      }else{
        items.add('forum-checkin', Button.component({
          icon: 'fas fa-calendar-check',
          className: 'Button Button--green',
          itemClassName: 'App-primaryControl',
          // style: 'width:190px',
          disabled: true
        }, app.translator.trans('ziven-checkin.forum.checked-in')));
      }

      $(".item-forum-checkin").insertBefore($(".item-newDiscussion"));
    }
  });
});